@media (min-width: 425px) {
    .iframe-video {
        width: 100%;
        height: 100%;
        min-height: 200px;
    }

    .table-cell-title {
        font-size: 1em;
        text-overflow: ellipsis;
        max-width: 300px;
        white-space: nowrap;
        overflow: hidden;
    }
}

@media (min-width: 768px) {
    .iframe-video {
        width: 100%;
        height: 100%;
        min-height: 300px;
    }

    .iframe-box {
        padding: 0 50px;
    }

    .table-cell-title {
        max-width: 250px;
    }
}

@media (min-width: 1024px) {
    .iframe-video {
        width: 100%;
        height: 100%;
        min-height: 300px;
    }

    .iframe-box {
        padding: 0 50px;
    }


    .table-cell-title {
        max-width: 100px;
    }
}

@media (min-width: 1440px) {
    .iframe-video {
        width: 100%;
        height: 100%;
        min-height: 600px;
        max-width: 1200px;
    }

    .iframe-box {
        padding: 0 100px;
    }

    .table-cell-title {
        max-width: 200px;
    }
}

.material-link {
    display: flex;
    align-items: center;
    gap: 5px;
    text-decoration: none;
}

.material-link:hover {
    text-decoration: underline;
}

.homework-status-is-being-checked {
    background-color: #188ae2;
    padding: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.homework-status-no-homework-done {
    background-color: #fe7c96;
    padding: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.homework-status-note {
    background-color: #1bcfb4;
    padding: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.grid-item {
    background-color: white;
    position: relative;
    padding: 50px 25px 90px 25px;
}

.date {
    border-top: 1px solid #ebedf2;
    border-bottom: 1px solid #ebedf2;
    padding: 10px 0;
    align-items: center;
}

.link-container {
    justify-content: flex-start;
    left: 25px;
    position: absolute;
    bottom: 30px;
}




